import useStyles from './styles'
import { TextField, Button, Typography, Paper } from '@material-ui/core'
import Fab from "@material-ui/core/Fab";
import { useState, useEffect } from 'react'
import FileBase from 'react-file-base64'
import { useDispatch, useSelector } from 'react-redux'
import { createPost, updatePost } from '../../actions/posts'
import AddPhotoAlternateIcon from "@material-ui/icons/AddPhotoAlternate";

export function Form({ currentId, setCurrentId }) {
  const [postData, setPostData] = useState({
    creator: '',
    title: '',
    message: '',
    tags: '',
    selectedFile: ''
  })
  const post = useSelector(state => currentId ? state.posts.posts.find(post => post._id === currentId) : null)
  const dispatch = useDispatch()
  const classes = useStyles()
  useEffect(() => {
    if (post) setPostData(post)

  }, [post])
  const handleSubmit = (ev) => {
    ev.preventDefault()
    if (currentId) {
      dispatch(updatePost(currentId, postData))
    }
    else {
      dispatch(createPost(postData))
    }
    clear();
  }
  const clear = () => {
    setCurrentId(0);
    setPostData({ creator: '', title: '', message: '', tags: '', selectedFile: '' });
  }

  // const handleUpload = (ev) => {
  //   var file = ev.target.files[0];
  //   const reader = new FileReader();
  //   var url = reader.readAsDataURL(file);

  //   reader.onloadend = function (e) {
  //     setPostData({ ...postData, selectedFile: [reader.result] });
  //   }.bind(this);
  //   console.log(url); // Would see a path?
  //   setPostData({ ...postData, selectedFile: ev.target.files[0] });

  // }
  return (
    <Paper className={classes.paper}>
      <form autoComplete="off" noValidate className={`${classes.root} ${classes.form}`} onSubmit={handleSubmit}>
        <Typography variant="h6">{(!currentId) ? 'Creating' : 'Editing'} a Memory</Typography>
        <TextField name="creator" variant="outlined" label="Creator" fullWidth value={postData.creator} onChange={(ev) => setPostData({ ...postData, creator: ev.target.value })} />
        <TextField name="title" variant="outlined" label="Title" fullWidth value={postData.title} onChange={(ev) => setPostData({ ...postData, title: ev.target.value })} />
        <TextField name="message" variant="outlined" label="Message" fullWidth value={postData.message} onChange={(ev) => setPostData({ ...postData, message: ev.target.value })} />
        <TextField name="tags" variant="outlined" label="Tags" fullWidth value={postData.tags} onChange={(ev) => setPostData({ ...postData, tags: ev.target.value.split(',') })} />
        <div className={classes.fileInput}>
          {/* <input
            accept="image/*"
            className={classes.input}
            id="contained-button-file"
            multiple
            type="file"
            onChange={handleUpload}
          />
          <label htmlFor="contained-button-file">
            <Fab component="span" className={classes.button}>
              <AddPhotoAlternateIcon />
            </Fab>
          </label> */}
          <FileBase style={{ position: 'absolute' }} type="file" className={classes.fileBase} multnple={false} onDone={({ base64 }) => setPostData({ ...postData, selectedFile: base64 })} />
        </div>
        <Button className={classes.buttonSubmit} color="primary" variant="contained" size="large" type="submit" fullWidth>Submit</Button>
        <Button variant="contained" size="small" color="secondary" onClick={clear} fullWidth>Clear</Button>

      </form>
    </Paper>
  )
}
